"use client";
import { useState, useEffect } from "react";
import Link from "next/link";

const Navbar = () => {
  const [mounted, setMounted] = useState(false);
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const [isAcademicsMenuOpen, setIsAcademicsMenuOpen] = useState(false);
  const [activeSection, setActiveSection] = useState("home");

  useEffect(() => {
    setMounted(true);
  }, []);

  const toggleMobileMenu = () => {
    setIsMobileMenuOpen(!isMobileMenuOpen);
  };

  const toggleAcademicsMenu = () => {
    setIsAcademicsMenuOpen(!isAcademicsMenuOpen);
  };

  if (!mounted) {
    return null;
  }

  return (
    <nav className="bg-blue-600">
      <div className="container mx-auto px-4">
        <div className="flex justify-between items-center h-16">
          <Link
            href="/"
            className="hidden md:flex font-mono font-bold tracking-wider text-white text-xl"
          >
            AIMSR
          </Link>

          {/* Desktop Links */}
          <div className="hidden md:flex space-x-8">
            <Link
              href="/about"
              className={`text-white hover:underline ${activeSection === "about" ? "underline" : ""}`}
              onClick={() => setActiveSection("about")}
            >
              About
            </Link>
            <div className="relative">
              <button
                className={`text-white hover:underline ${activeSection === "academics" ? "underline" : ""}`}
                onClick={() => {
                  setActiveSection("academics");
                  toggleAcademicsMenu();
                }}
              >
                Academics
              </button>
              {isAcademicsMenuOpen && (
                <div className="absolute top-full left-0 bg-white shadow-lg rounded-md py-2 w-48 z-10">
                  <Link
                    href="/academics/courses"
                    className="block px-4 py-2 text-black hover:bg-gray-100"
                    onClick={() => setIsAcademicsMenuOpen(false)}
                  >
                    Courses
                  </Link>
                  <Link
                    href="/academics/faculty"
                    className="block px-4 py-2 text-black hover:bg-gray-100"
                    onClick={() => setIsAcademicsMenuOpen(false)}
                  >
                    Faculty
                  </Link>
                </div>
              )}
            </div>
            <Link
              href="/contact"
              className={`text-white hover:underline ${activeSection === "contact" ? "underline" : ""}`}
              onClick={() => setActiveSection("contact")}
            >
              Contact
            </Link>
          </div>

          {/* Mobile Menu */}
          <div className="md:hidden">
            <button onClick={toggleMobileMenu} className="text-white p-2">
              <svg
                className="w-6 h-6"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={2}
                  d="M4 6h16M4 12h16M4 18h16"
                />
              </svg>
            </button>
            {isMobileMenuOpen && (
              <div className="absolute top-16 right-0 left-0 bg-white shadow-lg">
                <Link
                  href="/about"
                  className="block px-4 py-2 hover:bg-gray-100"
                  onClick={toggleMobileMenu}
                >
                  About
                </Link>
                <Link
                  href="/academics"
                  className="block px-4 py-2 hover:bg-gray-100"
                  onClick={toggleMobileMenu}
                >
                  Academics
                </Link>
                <Link
                  href="/contact"
                  className="block px-4 py-2 hover:bg-gray-100"
                  onClick={toggleMobileMenu}
                >
                  Contact
                </Link>
              </div>
            )}
          </div>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
